.users{
    max-width: 1000px;
    display: flex;
    flex-wrap: wrap;
}
.users .item + .item{
    margin-top: 1em;
}
.item{
    flex: 0 1 48%;
    display: flex;
    align-items: center;
    column-gap: 2em;
}
.avatar{
    width: 100px;
    height: 100px;
    border-radius: 100%;
    overflow: hidden;
}
.avatar img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.info button{
    margin-top: .6em;
}
.name{
    display: block;
    font-size: 1.1em;
    font-weight: 500;
}
.img{
    cursor: pointer;
}